import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import About from '@/components/about/about'
import Contact from '@/components/contact/contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Hello',
      icon: 'face',
      component: Hello
    },
    {
      path: '/about',
      name: 'About',
      icon: 'favorite',
      component: About
    },
    {
      path: '/contact',
      name: 'Contact',
      icon: 'contact_mail',
      component: Contact
    }
  ]
})



