<template>
  <div class="about">
    <h1>Family</h1>
    <div class="checkboxes">
      <label class="ckb-label" v-for="(user, idx) in users">
        <input class="ckb-input" type="checkbox" v-model="items" :id="idx" :key="idx"
               :value="user">
        <span>
          <img :src="user.avatar" alt="">
        </span>
      </label>
    </div>
    <div class="family" v-for="(item,idx) in items">
      <h5>{{ item.name }}</h5>
      <p>{{ item.comment }}</p>
    </div>
  </div>
</template>
<style lang="scss">
  .family {
    background-color: #00acc1;
    padding: 0.5rem 1rem;
    margin: 2rem;
  }
  .checkboxes {
    box-sizing: border-box;
    margin: auto;
    .ckb-label {
      cursor: pointer;
      span {
        overflow: hidden;
        max-width: 100px;
        max-height: 100px;
        text-align: center;
        display: inline-block;
        /*padding: 0 10px;*/
        border: 1px solid red;
        margin: 3px;
        opacity: 0.7;
        transition: opacity 0.5s;
        &:hover {
          opacity: 1;
        }
      }
      .ckb-input {
        visibility: hidden;
        border: none;
        height: 0;
        width: 0;
        &:checked ~ span {
          border-color: green;
          box-shadow: 0 0 6px rgba(255, 255, 255, 0.5);
          opacity: 1;
        }
      }
    }
  }
</style>
<script>
  export default {
    name: 'About',
    data () {
      return {
        items: [],
        users: [
          {
            name: 'Wolfgang',
            id: 1,
            comment: 'He\'s the boss',
            avatar: 'https://api.adorable.io/avatars/100/mm@adorable.io.png'
          },
          {
            name: 'Elisabeth',
            id: 2,
            comment: 'She\'s the boss too',
            avatar: 'https://api.adorable.io/avatars/100/huhu@adorable.io.png'
          },
          {
            name: 'Linda',
            id: 3,
            comment: 'She wants to be the boss',
            avatar: 'https://api.adorable.io/avatars/100/lol@adorable.io.png'
          }
        ]
      }
    },
    computed: {
      getX () {
        return this.$loveIt.x
      }
    },
    created () {
      console.log(this.$loveIt ? 'YES' : 'NO')
      console.log(this.$loveIt.showMe())
    }
  }
</script>



