class PluginTest {
  constructor (x = 0, y = 0) {
    this.x = x
    this.y = y
  }

  showMe () {
    return `
    This is a tes to create a JavaScript Class with ${this.x} and ${this.y}
    `
  }
}

export default {
  install (Vue, name = '$pTest', options = null) {
    let x = 0
    let y = 0

    if (options) {
      const {a, b} = options
      x = a
      y = b
    }

    Object.defineProperty(Vue.prototype, name, {value: new PluginTest(x, y)})
  }
}



