import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  name: 'Vue Mobile'
}

const actions = {
  renameApp ({commit, state}) {
    console.log('Rename')
  }
}

const mutations = {
  renameApp (state, name) {
    state.name = name
  }
}

const getters = {
  getAppName (state) {
    return state.name
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



