// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuetify from 'vuetify'
import './stylus/main.styl'
import PluginTest from '@/globals/plugin-test'
import store from './store/vue-mobile-store'

Vue.use(PluginTest, '$loveIt', {a: 15, b: 10})
import App from './App'
import router from './router'

import Test from './components/globals/test.vue'

Vue.component('test', Test)

Vue.use(Vuetify)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App},
  created () {
    console.log(this.$loveIt ? 'YES' : 'NO')
    console.log(this.$loveIt.showMe())
  }
})



