<template>
  <div class="contact">
    <h1>Contact</h1>
    <form>
      <v-text-field
        v-model="name"
        label="Navn"
      ></v-text-field>
      <div class="message">{{ validation.firstError('name') }}</div>
    </form>

  </div>
</template>
<script>
  import SimpleVueValidator from 'simple-vue-validator'
  const Validator = SimpleVueValidator.Validator

  SimpleVueValidator.extendTemplates({
    required: 'det ser ikke bra ut',
    size: 'minst tegn'
  })
  export default {
    name: 'Contact',
    mixins: [SimpleVueValidator.mixin],
    validators: {
      name (value) {
        return Validator.value(value).required().size(8)
      }
    },
    methods: {
      submit () {
        this.$validator.validateAll()
      },
      clear () {
        this.name = ''
        this.email = ''
        this.select = null
        this.checkbox = null
        this.$validator.clean()
      }
    },
    data () {
      return {
        name: '',
        email: '',
        select: null,
        items: [
          'item 1',
          'item 2',
          'item 3',
          'item 4'
        ]
      }
    }
  }
</script>
<style>
  .contact {
  }
</style>



